
import api from "@/api";
import lottie from 'vue-lottie'
import loading from "./json/loadingBlue.json"

export default {
    name: "index",
    mixins: [api],
    components: {
        lottie
    },
    data(){
        return{
            defaultOptions:{animationData:loading,loop:true,autoplay:true},
            defaultAnim:'',
            show: true,
            list: [
                {id: 0, img: require('@/assets/images/51290b9fb3b08b5ca7baa78147bf2b44.webp.jpg'), description: ''},
                {id: 1, img: 'http://qiniu.longxin.press/2021022001_1613784863193.jpg', description: ''},
                {id: 2, img: 'http://qiniu.longxin.press/BlogFile--1-uN.jpg', description: ''},
                {id: 3, img: 'http://qiniu.longxin.press/BlogFile--3-53.jpg', description: ''},
                {id:4, img:'http://qiniu.longxin.press/HaloBlog/t01322912b97df92883_1584598782222.jpg', description: ''}
            ],
        }
    },
    mounted() {
        /*this.cover().then(res => {
            this.list = res.data
        })*/
        window.addEventListener('scroll', this.scrollEvent)
    },
    methods: {

        handleAnimation(anim){
            this.defaultAnim = anim;
        },
        sleep(duration) {
            return new Promise(resolve => {
                setTimeout(resolve, duration)
            })
        },
        scrollEvent(){
            if (document.documentElement.scrollTop >= window.innerHeight) {
                document.getElementById('screen').style.opacity = 0;
            } else {
                document.getElementById('screen').style.opacity = 1;
            }
        },
        async toCenter() {
            let scrollTop = document.documentElement.scrollTop;
            let innerHeight = window.innerHeight
            if (scrollTop < innerHeight) {
                let mistake = innerHeight - scrollTop;
                if (mistake > 10) {
                    mistake = mistake / 10;
                } else {
                    mistake = 2;
                }
                document.documentElement.scrollTop = document.documentElement.scrollTop + mistake;
                await this.sleep(10);
                await this.toCenter()
            }
        }
    },
}
