
import Vue from 'vue'
import Viewer from 'v-viewer'
import 'viewerjs/dist/viewer.css'
Vue.use(Viewer)
Viewer.setDefaults({
    Options: { zIndex: 4 ,'inline': true, 'button': true, 'navbar': true, 'title': true, 'toolbar': true, 'tooltip': true, 'movable': true, 'zoomable': true, 'rotatable': true, 'scalable': true, 'transition': true, 'fullscreen': true, 'keyboard': true, 'url': 'data-source' }
})

export default {
    name: "index",
    props: {
        list: [],
    },
    data() {
        return {
            show: false,
            imgList: []
        }
    },
    mounted() {
        setTimeout(this.list.forEach( item => {
            this.imgList.push(item.cover)
        }), 2000)

    },
    methods: {
    }
}
